<template>
    <a-form>
        <a-form-item label="用户名">
            <a-input v-model:value="form.username"></a-input>
        </a-form-item>
        <a-form-item label="密码">
            <a-input-password v-model:value="form.password"></a-input-password>
        </a-form-item>
        <a-form-item>
            <a-button type="primary" @click="handleSubmit">登录</a-button>
        </a-form-item>
    </a-form>
</template>

<script setup lang="ts">
import { login } from '@/api/login';
import { reactive } from 'vue';
import { useRoute } from 'vue-router';

const route = useRoute();

const form = reactive({
    username: '',
    password: '',
});

async function handleSubmit() {
    const res = await login(form.username, form.password);

    const token = res.data.accessToken;

    localStorage.setItem('token', token);
    localStorage.setItem('x_jwt', token);

    window.location.href = '/aiassistant';
}
</script>
