export function login(username: string, password: string) {
    return fetch('https://pre-hwork.haier.net/gw/login/api/v2/token', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({
            terminal: 'pc',
            username,
            password,
        }),
    }).then((response) => response.json());

    // return request('https://pre-hwork.haier.net/gw/login/api/v2/token', {
    //     method: 'POST',
    //     data: {
    //         terminal: 'pc',
    //         username,
    //         password,
    //     },
    // });
}
